<template>
   <div class="ContMovies">
      <div class="BxMovie" v-if="getFistData === 'NFD' || getFistData === undefined">
         <div class="BxNotReady">
           Not Ready Yet, Please try again in a few minutes
         </div>
      </div>
      <div 
         class="BxMovie" 
         v-if="getFistData !== 'NFD' && getFistData !== undefined">

         <div 
            class="BxFrame" 
            v-for="(n, i) in getFistData" 
            :key="i" :class="{ BxPreload: getLoader }"
            @click="SelectedMovie(n.imdb)"
            >
            <div class="BxFrame__Poster">
               <!-- <img src="../../../assets/image/cyAlYJUrE77k19MoH5pcMakh6YD.jpg" alt=""> -->
               <img v-if="n.poster" :src="n.poster" alt="poster">
               <img v-else src="../../../assets/image/opss.png" alt="poster">
            </div>
            <div class="BxFrame__InfoUp">
               <p class="BxFrame__InfoUp--quality">{{n.quality | NotEmpty}}</p>
               <div class="BxFrame__InfoUp--rating">
                   <i class="fa fa-star"></i>
                   <p>{{n.rating | NotEmpty}}</p>
               </div>
              
            </div>
            <div class="BxFrame__InfoBottom">
               <span class="BxFrame__InfoBottom--Titles">
                  <p>{{n.title | NotEmpty}}</p>
                  <p>( {{n.year | NotEmpty}} )</p>
               </span>
            </div>
         </div>

         <div class="BxFrame" 
            v-for="(s, i) in 5" :key="'A'+ i" 
            style="visibility: hidden;height:20px !important;margin-bottom:0 !important;margin-top:0 !important"
            v-show="!getLoader">
         </div>
      </div>

      <div class="BXpaginations" v-if="getFistData !== 'NFD' && getFistData !== undefined">
         <v-app>
            <v-pagination 
               color="#4dcba1" dark circle
               prev-icon="mdi-menu-left"
               next-icon="mdi-menu-right"
               v-model="currentPage" 
               :length="getLastPage"  
               :total-visible="9">
            </v-pagination>
         </v-app>
      </div> 
   </div>
</template>


<script>
   import { AllFilter } from '../../Mixins/MixFilter'
   export default {
      mixins: [AllFilter],
      props:[
         'getLoader'
      ],
      data: () => ({ 
         currentPage: 1,
      }),
      computed: {
         //Catch data movie from State FirstData
         getFistData(){
            return this.$store.state.FirstData ? this.$store.state.FirstData.data:'NFD'
         },

         isMaxPage(){
            return this.currentPage >= this.getLastPage
         },

         //Calculate Max number on Pagination
         getLastPage(){
            return Math.ceil(this.getFistData.length * 3)
         }

      },
      methods: {
         LoadList(){
            const varPage = this.currentPage
            this.$store.dispatch('GET_FIRSTDATA', varPage);
            //This active loader to true
            this.$store.commit('MutLoaderDt', true);
         },
         SelectedMovie(imdb){
            const IdMovie = imdb
            this.$store.commit('MutPlayerStat', true);
            this.$store.dispatch('GET_PLAYER_DATA', IdMovie);
            //This active loader to true
            this.$store.commit('MutLoaderDt', true);
         }
      },
      watch: {
         currentPage(val){
            // This function for monitor if there any change on currentPage for pagination,
            // if change will be trigger method LoadList
            if(val){
               this.LoadList()
            }
         }
      },
   }
</script>


